.sidebar {
  text-align: center;
  margin: 90px 24px 0 0;
  width: 100%;
  max-width: 246px;
  // outline: 4px solid red
  @include r(767) {
    max-width: 100%;
    margin: 0 0 45px; }
  &__inner {
    &.is-absolute {
      position: absolute;
      @include r(767) {
        position: static; } }
    &.is-fixed {
      position: fixed;
      @include r(767) {
        position: static; } } }
  &__title {
    margin-bottom: 7px; }
  &__date {
    margin-bottom: 2px; }
  &__note {
    display: flex;
    justify-content: space-between;
    font-size: rem(14);
    @include proxima-it; }
  &__group {
    margin: 0 auto 25px;
    @include r(767) {
      max-width: 220px; }
    &--more-mb {
      margin-bottom: 35px; } }
  &__button {
    max-width: 200px;
    margin: 0 auto 30px;
    @include r(767) {
      max-width: 320px; } }
  &__list {
    max-width: 200px;
    margin: 0 auto;
    @include r(767) {
      max-width: 320px; } }
  &__option {
    margin-bottom: 7px;
    &:last-child {
      margin-bottom: 0; } }
  &__link {
    display: block;
    padding: 7px 11px;
    text-decoration: none;
    text-align: left;
    @include proxima-sb;
    color: $black;
    border: 1px solid $blue;
    border-radius: 5px;
    &:hover,
    &:focus,
    &.is-active {
      background: $blue;
      color: #fff; } }
  hr {
    margin: 25px auto 35px;
    width: 74px;
    border: 0;
    border-bottom: 3px solid $yellow; } }
